import React, { useState } from "react";


function Likes() {
    const [count,setCount]=useState(0);


    return (
        
            <div className="like-counter">
                
                <button  onClick={() => setCount(count + 1)}>Like: {count}</button>

            </div>
        
    )
}



export default Likes;


